// Bot responses for different message types
const getBotResponse = (message: string): string => {
  const lowerMessage = message.toLowerCase();
  
  if (lowerMessage.includes('hello') || lowerMessage.includes('hi') || lowerMessage.includes('hey')) {
    return "Hello! Welcome to our WhatsApp Business support. How can I help you today?";
  }
  
  if (lowerMessage.includes('price') || lowerMessage.includes('cost') || lowerMessage.includes('pricing')) {
    return "Our pricing starts at $29/month for the basic plan. Would you like me to send you detailed pricing information?";
  }
  
  if (lowerMessage.includes('help') || lowerMessage.includes('support')) {
    return "I'm here to help! You can ask me about our services, pricing, or technical support. What specific assistance do you need?";
  }
  
  if (lowerMessage.includes('order') || lowerMessage.includes('delivery')) {
    return "For order inquiries, I can help you track your shipment or update delivery details. Please provide your order number.";
  }
  
  if (lowerMessage.includes('thank') || lowerMessage.includes('thanks')) {
    return "You're welcome! Is there anything else I can help you with today?";
  }
  
  if (lowerMessage.includes('bye') || lowerMessage.includes('goodbye')) {
    return "Thank you for contacting us! Have a great day and feel free to reach out anytime.";
  }
  
  // Default response
  return "Thank you for your message! Our team will get back to you shortly. In the meantime, you can browse our FAQ or contact our support team directly.";
};

/**
 * Send a message and get bot response
 */
export async function sendMessage(message: string): Promise<ApiResponse<Message>> {
  try {
    await simulateDelay(800); // Simulate API call delay
    
    if (!message || message.trim().length === 0) {
      return {
        success: false,
        error: 'Message cannot be empty'
      };
    }
    
    const botResponse: Message = {
      id: Date.now().toString(),
      text: getBotResponse(message),
      sender: 'bot',
      timestamp: new Date().toISOString(),
      status: 'delivered'
    };
    
    return {
      success: true,
      data: botResponse
    };
  } catch (error) {
    console.error('Error sending message:', error);
    return {
      success: false,
      error: 'Failed to send message. Please try again.'
    };
  }
}

