/**
 * Add a new contact
 */
export async function addContact(contact: Omit<Contact, 'id'>): Promise<ApiResponse<Contact>> {
  try {
    await simulateDelay(700);
    
    const newContact: Contact = {
      ...contact,
      id: Date.now().toString()
    };
    
    mockContacts.push(newContact);
    
    return {
      success: true,
      data: newContact
    };
  } catch (error) {
    console.error('Error adding contact:', error);
    return {
      success: false,
      error: 'Failed to add contact. Please try again.'
    };
  }
}

/**
 * Update contact status
 */
export async function updateContactStatus(contactId: string, status: 'active' | 'inactive'): Promise<ApiResponse<Contact>> {
  try {
    await simulateDelay(400);
    
    const contactIndex = mockContacts.findIndex(c => c.id === contactId);
    if (contactIndex === -1) {
      return {
        success: false,
        error: 'Contact not found'
      };
    }
    
    mockContacts[contactIndex].status = status;
    
    return {
      success: true,
      data: mockContacts[contactIndex]
    };
  } catch (error) {
    console.error('Error updating contact:', error);
    return {
      success: false,
      error: 'Failed to update contact status.'
    };
  }
}

/**
 * Send bulk message to multiple contacts
 */
export async function sendBulkMessage(contactIds: string[], message: string): Promise<ApiResponse<{ sent: number; failed: number }>> {
  try {
    await simulateDelay(1200);
    
    if (!message || message.trim().length === 0) {
      return {
        success: false,
        error: 'Message cannot be empty'
      };
    }
    
    // Simulate some failures in bulk messaging
    const sent = Math.floor(contactIds.length * 0.95); // 95% success rate
    const failed = contactIds.length - sent;
    
    return {
      success: true,
      data: { sent, failed }
    };
  } catch (error) {
    console.error('Error sending bulk message:', error);
    return {
      success: false,
      error: 'Failed to send bulk message. Please try again.'
    };
  }
}
